import React from 'react';
import Pic from './left/pic';
import styles from '../styles/homeLeft.module.css';
import SocialMedia from './left/socialMedias';

function HomeLeft() {

  return (
    <React.Fragment>
      <div className={styles.leftContainer}>
        <Pic imageSrc={process.env.REACT_APP_MY_PIC_LINK}></Pic>
        <SocialMedia />
      </div>
    </React.Fragment>
  );
}

export default HomeLeft;