import React from 'react';
import styles from '../../styles/right/title.module.css';

function Title() {
  return (
    <div className={styles.titleContainer}>
      <div className={styles.innerDiv}>
        <h1 className={styles.h1}>Honey Dev P</h1>
        <h4 className={styles.h4}>Software Engineer</h4>
        <p>
          Innovative optimized solution seeker. Excited to be at the deployment phase of my career as a web
          developer. I am ambitious, adventurous, assiduous and love working in company with a positive
          ethical stance.
      </p>
      </div>
    </div>
  )
}

export default Title;