import React from 'react';
import classnames from 'classnames';
import styles from '../../styles/left/pic.module.css';

function Pic(props) {
  return (
    <React.Fragment>
      <div className={classnames(styles.imageContainer)}>
        <img className={styles.image} src={props.imageSrc}></img>
      </div>
    </React.Fragment>
  );
}

export default Pic;