import React from 'react';
import { AiFillLinkedin, AiFillSkype, AiFillTwitterCircle } from "react-icons/ai";
import { MdMail } from 'react-icons/md';
import { TiLocation } from 'react-icons/ti'

import styles from '../../styles/left/socialMedia.module.css';

function SocialMedia() {

  function onEmailIconClick() {
    window.location.href = "mailto:honeydevp@hotmail.com";
  }

  function onLinkedInClick() {
    window.open('https://www.linkedin.com/in/hodev9309/');
  }

  function onTwitterIconClick() {
    window.open('https://twitter.com/honeydevp');
  }

  function onSkypeIconClick() {
    window.location = 'skype:live:honeydevp?chat';
  }

  function onLocationClick() {
    let sUrl = 'https://www.google.com/maps/place/New+Delhi,+Delhi/@28.5272803,77.0688997,11z/data=!3m1!4b1!4m5!3m4!1s0x390cfd5b347eb62d:0x52c2b7494e204dce!8m2!3d28.6139391!4d77.2090212'
    window.open(sUrl);
  }


  return (
    <div className={styles.socialMediaContainer}>

      <div className={styles.divEach} onClick={onEmailIconClick}>
        <MdMail />
        <p className={styles.p}>honeydevp@gmail.com</p>
      </div>

      <div className={styles.divEach} onClick={onLinkedInClick}>
        <AiFillLinkedin />
        <p className={styles.p}>linkedin.com/in/hodev9309</p>
      </div>

      <div className={styles.divEach} onClick={onTwitterIconClick}>
        <AiFillTwitterCircle />
        <p className={styles.p}>https://twitter.com/honeydevp</p>
      </div>

      <div className={styles.divEach} onClick={onSkypeIconClick}>
        <AiFillSkype />
        <p className={styles.p}>live:honeydevp</p>
      </div>

      <div className={styles.divEach} onClick={onLocationClick}>
        <TiLocation />
        <p className={styles.p}>New Delhi, IndiaðŸ’™</p>
      </div>
    </div >
  );
}

export default SocialMedia;